:root {
  background-color: #000;
  color: #fff;
  font-family: monospace;
  color: #fff;
  overflow: hidden;
}
body {
  margin: 0;
}

#play {
  position: absolute;
  cursor: pointer;
  font: inherit;
  font-size: 20vw;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.5s ease-out;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}

#app-hidden {
  z-index: -1;
}

#app {
  position: relative;
  min-height: 100vh;
  z-index: auto;
}

canvas {
  cursor: url(data:image/png;base64,),
    pointer;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  position: absolute;
}

canvas[data-snowflake] {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

canvas[data-scroller] {
  width: 100%;
  height: auto;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

audio {
  opacity: 0.06;
  pointer-events: none;
}
